body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media only screen and (max-width: 600px) {
  body {
    background-color: rgb(230, 240, 243);
  }
  #imgborder{
    min-width: 400px;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.containerA{
  margin: 50px;
}

#imgborder img{
  border: 4px solid rgb(49, 85, 80);
  margin: 20px;
}

.square{
  min-height:60px;
  min-width:60px;
  margin:0px;
  padding:0px;
  vertical-align: top;
  font-size: 35px;
}

.header{
  position: fixed;
  top: 0px;
  background: #f8f5f5;   
  display: block;
  width: 100%;
  margin-bottom:200px;
}

.containerMain{
  margin-top: 200px;
  margin: 10px;
  border: 10px solid rgb(193, 193, 202);
  position: static;
}
#contactContainer{
  border-bottom: 2px solid rgb(193, 193, 202);
  font-weight: bold;
}
h2{
  color:rgb(88, 88, 243); 
}